
//variables
const galeria = document.querySelector('.contenido-galeria');
const botonDarkMode = document.querySelector('#check');

const tarjeta = document.querySelector('.tarjeta');
const btn = document.querySelectorAll('.btn')
const tarjetasSkill = document.querySelectorAll('.tarjetas-skills')
const container = document.querySelectorAll('.container')

//verificar si esto en la raiz
if(window.location.pathname == "/index.html" || window.location.pathname =="/"){
    var intervalo = setInterval(moverDeracha,2500);//lo decklaramos en var pa resetearlo si movemos mientras se ejecuta

}

var cont = 0; 
const img = ["build/img/0.webp",
        "build/img/1.webp",
        "build/img/2.webp",
        "build/img/3.webp",
        "build/img/4.webp",
        "build/img/5.webp",
        "build/img/6.webp",
        "build/img/7.webp",
        "build/img/8.webp",
        "build/img/9.webp",
        "build/img/10.webp",
        "build/img/11.webp"];

if(window.location.pathname == "/index.html" || window.location.pathname =="/"){
    document.addEventListener('DOMContentLoaded', function(){
    document.imagen.classList.add('slide');
    document.imagen.src = img[cont];

    darkMode()
    
    

});


}


const derecha = document.querySelector('#derecha');
const izquierda = document.querySelector('#izquierda');

function moverDeracha(){

    if(cont<img.length){
        
        document.imagen.src = img[cont];
        
        
        cont++;

    }else{
        cont=0;
        
    }
}
function moverIzquierda(){
    cont--;
    if(cont<0){
        cont = img.length;
        
    }else{
        document.imagen.src = img[cont];
    }
}

derecha.addEventListener('click', () => {
    clearInterval(intervalo);
    moverDeracha();
    intervalo = setInterval(moverDeracha, 2500)
    
    
    });

izquierda.addEventListener('click', () => {
    clearInterval(intervalo);
    moverIzquierda();
    intervalo = setInterval(moverDeracha, 1800)
});






function limpiarHTML(elem){
    while(elem.firstChild){
        setTimeout(() => {
            elem.removeChild(elem.firstChild);
        }, 3000);
    }
    
    
}



function rad(grados){
    var rad = (Math.PI/180)*grados;
    return rad;
}



function darkMode() {

const prefiereDarkMode = window.matchMedia('(prefers-color-scheme: dark)');


if(prefiereDarkMode.matches) {
    document.body.classList.add('dark-mode');
    
} else {
    document.body.classList.remove('dark-mode');
}

prefiereDarkMode.addEventListener('change', function() {
    if(prefiereDarkMode.matches) {
        document.body.classList.add('dark-mode');
        
        
    } else {
        document.body.classList.remove('dark-mode');
    }
});

    botonDarkMode.addEventListener('click', function() {
        document.body.classList.toggle('dark-mode');
        
        tarjeta.classList.toggle('tarjeta-white');


        btn.forEach(b=> {
            b.classList.toggle('btn-shadow');
        });
        tarjetasSkill.forEach(skill => {
            skill.classList.toggle('tarjetas-skills-w')
            skill.classList.toggle('card-shadow');
        });
    
        container.forEach(b=> {
        b.classList.toggle('container-w');
        });

        //el if y el togle hacen practicamente lo mismo
        /*if(tarjeta.classList.contains('tarjeta-white')){
            tarjeta.classList.remove('tarjeta-white');
            console.log("holi")
        }else{
            tarjeta.classList.add('tarjeta-white');
        }*/

    });

    
    
}


